.App {
    margin: 0;
    padding: 0;
    overflow: hidden;
    /* align-items: center; */
}

.container {
    width: 100%;
    height: max-content;
    /* padding: 20px; */
    margin-top: 40px;
}

.img img {
    object-fit: contain;
    height: 200px;
    width: 200px;
    /* position: absolute; */
}

.store {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 2fr));
    grid-gap: 20px;
}

.card {
    position: relative;
    border: 2px solid rgba(0, 0, 0, 0.452);
    border-radius: 10px;
    height: max-content;
    display: flex;
    padding: 15px;
    flex-direction: column;
    align-items: center;
    transition: 250ms all ease-in;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.02);
}

.info {
    text-align: center;
    width: 60%;
}

.title p {
    font-size: 18px;
    font-weight: 600;
}

.btn {
    align-items: center;
}

.btn a {
    text-decoration: none;
    padding: 7px 17px;
    background-color: #272727;
    border-radius: 8px;
    cursor: pointer;
    color: white;
    border: 2px solid #272727;
    font-weight: 600;
}

.btn a:hover {
    transition: 250ms all ease-in;
    color: #272727;
    background-color: white;
}